#main-nav {
    background-color:rgb(1, 1, 67);
}

#main-nav ul {
    margin:0;
    padding-left:0;
}

#main-nav li {
    list-style-type:none;
    padding:5px;
    flex:1;
    border-bottom:1px solid white;
    text-align:center;
}

#main-nav li a {
    text-decoration: none;
    color:white;
}

#main-nav li a:hover {
    color:#f4d570;
}

#main-nav .hide-small {
    display:none;
}

#toggle-nav p { 
  margin: 0; 
}

#toggle-nav {
    display: block;
    color: white;
    text-decoration: none;
    font-size: 2em;
}

@media screen and (min-width: 768px) {
    #toggle-nav {
        display: none;
    }

    #main-nav button {
        display:none;
    }

    #main-nav .hide-small {
        display:flex;
    }

    #main-nav li {
        border-bottom:0;
    }
  }